











































































































































































    import Vue from 'vue';
    import ApplicationWrapper from '../partials/ApplicationWrapper.vue';

    export default Vue.extend({
	    components: {
            ApplicationWrapper
        },

        data() {
            return {

            };
        },

        created() {
            this.$onSocket('open', () => {
                this.$emitSocket('register', {
                    username: this.$store.state.user.username
                });
            });
        },

        destroyed() {
            this.$emitSocket('unregister');
        }
    });
